import { getRandomEventPoint } from '../mock/event-point-data';
import { mockEventPointDestinations } from '../mock/destination';
import { mockEventPointOffers } from '../mock/offers';

const POINTS_COUNT = 4;

export default class PointsModel {
  points = Array.from({ length: POINTS_COUNT }, getRandomEventPoint);
  offers = mockEventPointOffers;
  destinations = mockEventPointDestinations;

  getPoints() {
    return this.points;
  }

  getOffers(){
    return this.offers;
  }

  getOffersByType(type){
    const allOffers = this.getOffers();
    return allOffers.find((el) => el.type === type);

  }

  getOffersById(type, pointsId){
    const offersType = this.getOffersByType(type).offers;
    //console.log(offersType); ÑÑÑ Ð¿ÑÐ¾Ð²ÐµÑÑÑ ÑÑÐ¾ Ð¿ÑÐ°Ð²Ð¸Ð»ÑÐ½Ð¾ Ð¿ÑÐ¸ÑÐ¾Ð´Ð¸Ñ Ð¼Ð°ÑÑÐ¸Ð² Ñ Ð¾ÑÐµÑÐ°Ð¼Ð¸
    const offersById = [];
    offersType.forEach((typedOffer) => {

      if(pointsId.some((pointOfferId) => pointOfferId === typedOffer.id)){
        offersById.push(typedOffer);

      }

    });
    //console.log(offersById); ÑÑÑ Ð¿ÑÐ¾Ð²ÐµÑÑÑ Ð¾ÑÑÐ¾ÑÑÐ¸ÑÐ¾Ð²Ð°Ð½Ð½ÑÐ¹ Ð¼Ð°ÑÑÐ¸Ð²
    return offersById;
  }

  getDestinations(){
    return this.destinations;
  }

  getDestinationsByName(pointsName){
    const allDestinations = this.getDestinations();
    //console.log(allDestinations.find((destination) => destination.name === pointsName).pictures); ÑÑÑ Ð¿ÑÐ¾Ð²ÐµÑÑÑ ÑÑÐ¾
    // destinations Ð¿Ð¾ Ð¸Ð¼ÐµÐ½Ð¸ ÑÐ¾ÑÐºÐ¸ Ð¿ÑÐ¸ÑÐ»Ð¸ Ð¿ÑÐ°Ð²Ð¸Ð»ÑÐ½Ð¾
    return [allDestinations.find((destination) => destination.name === pointsName)];
  }
}
