import AddEventPointView from '../view/add-event-point-view';
import EditEventPointView from '../view/edit-event-point-view';
import EventListView from '../view/event-list-view';
import EventPointView from '../view/event-point-view';
import { render } from '../render.js';

export default class EventPresenter {
  eventListComponent = new EventListView();

  constructor({ listContainer, pointModel }) {
    this.listContainer = listContainer;
    this.pointModel = pointModel;
  }

  init() {
    this.eventPoints = [...this.pointModel.getPoints()];
    render(this.eventListComponent, this.listContainer);
    render(new EditEventPointView(), this.eventListComponent.getElement());
    render(new AddEventPointView(), this.eventListComponent.getElement());

    for (let i = 0; i < this.eventPoints.length; i++) {
      //console.log(this.eventPoints[i].type); Ð¿ÑÐ¾Ð²ÐµÑÑÑ ÑÐ¸Ð¿ Ð¸ÑÐµÑÐ¸ÑÑÐµÐ¼Ð¾Ð¹ ÑÐ¾ÑÐºÐ¸
      //console.log(this.eventPoints[i].offers); Ð¿ÑÐ¾Ð²ÐµÑÑÑ ÐºÐ°ÐºÐ¸Ðµ Ð¾ÑÐµÑÑ ÑÐ¾Ð¾ÑÐ²ÐµÑÑÐ²ÑÑÑ Ð´Ð°Ð½Ð½Ð¾Ð¹ ÑÐ¾ÑÐºÐµ
      const eventOffers = [...this.pointModel.getOffersById(this.eventPoints[i].type, this.eventPoints[i].offers)];
      const eventDestinations = [...this.pointModel.getDestinationsByName(this.eventPoints[i].name)];
      render(
        new EventPointView({ point: this.eventPoints[i], offers: eventOffers, destinations: eventDestinations }),
        this.eventListComponent.getElement()
      );
    }
  }
}
